@import url(https://fonts.googleapis.com/css?family=Kulim+Park:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Kulim Park", sans-serif;background-color:cornflowerblue;height:100vh;margin:0;padding:2em}body .container{background-color:whitesmoke;max-width:600px;margin:50px auto;padding:2em 3em;border-radius:15px}body .container .password-area{display:block;width:100%;line-height:1.5;font-size:1.2em;margin:1em 0}body .container .length-slider{margin:1em 0.5em}body .container .char-options{margin:1em auto;display:flex;justify-content:space-evenly}body .container .password-button{display:block;width:100%;line-height:1.5;font-size:1.2em;border-radius:5px}

